import React from 'react'

import './Button.scss';

const ComponentView = (props) => {
  const { children = '', className = '', onClick = () => {} } = props;
  return (
    <div className={`outlined-button ${className}`} onClick={onClick}>
      <div className="button">
        { children }
      </div>
    </div>
  );
};

export default ComponentView;
